import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useNavigate } from 'react-router-dom'
import { Button } from 'semantic-ui-react'
import { signOut } from '../actions/signin'

const Nav = () => {

    const user = useSelector((store) => store.signIn.user)
    const [mobileNav, setmobileNav] = useState(true)

    const dispatch = useDispatch()
    const navigate = useNavigate()

    const handleSignout = () => {
        dispatch(signOut())
        navigate('/signin')
    }

    const toggleNav = (e) => {
        e.preventDefault()
        const navToggle = document.querySelector('.mobile-nav-toggle')
        const nav = document.querySelector('.primary-navigation')
        const visiblity = nav.getAttribute('data-visible')
        if (visiblity === "false") {
            nav.setAttribute("data-visible", true)
            navToggle.setAttribute("aria-expanded", true)
        } else {
            nav.setAttribute("data-visible", false)
            navToggle.setAttribute("aria-expanded", false)
        }
    }

    if (mobileNav) {
        return (
            <div className='primary-header flex bg-dark'>
                <p
                    className='fs-300 ff-serif text-white user-name'
                >Welcome <span
                    className='ff-serif fs-400 text-accent'
                >{user && user.text}
                    </span>
                    !</p>
                <button
                    onClick={(e) => toggleNav(e)}
                    aria-expanded='false'
                    className='mobile-nav-toggle'
                ><span
                    className='sr-only'
                >Menu</span></button>
                <div
                    datatype-visible='false'
                    className='nav primary-navigation underline-indicators flex'
                >
                    <Link
                        to='/'
                        exact='true'
                        className='ff-sans-cond uppercase text-white letter-spacing-2'
                    >
                        Home
                    </Link>
                    <Link
                        to='/new'
                        className='ff-sans-cond uppercase text-white letter-spacing-2'
                    >
                        New Poll
                    </Link>
                    <Link
                        to='/leaderboard'
                        className='ff-sans-cond uppercase text-white letter-spacing-2'
                    >
                        Leaderboard
                    </Link>
                    <Link
                        to='/signin'
                        onClick={() => handleSignout()}
                        className='container fs-400 ff-sans-cond'
                    >
                        <span className='text-accent'>Sign Out</span>
                    </Link>
                </div>
            </div>
        )
    }

    return (
        <div className='nav'>
            <Link
                to='/'
                exact='true'
                className='text-dark bg-accent container fs-400 ff-sans-cond'
            >
                Home
            </Link>
            <Link
                to='/new'
                className='text-dark bg-accent container fs-400 ff-sans-cond'
            >
                New Poll
            </Link>
            <Link
                to='/leaderboard'
                className='text-dark bg-accent container fs-400 ff-sans-cond'
            >
                Leaderboard
            </Link>
            <p
                className='fs-300 ff-serif'
            >Welcome {user && user.text} !</p>
            <Link
                to='/signin'
                onClick={() => handleSignout()}
                className='text-dark bg-accent container fs-400 ff-sans-cond'
            >
                Sign Out
            </Link>
        </div>
    )
}

export default Nav
